<template>
 <v-app>
    <v-content>

      <GmapMap
      :center="mapCenter"
      :zoom="mapZoom"
      map-type-id="roadmap"
      style="width: 100%; display: block; height: 100%;"
      :options="mapOptions"
    >
        <GmapMarker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        :icon="getIcon(m)"
        alignment="center"
        @click="display(index)"
        />
        </GmapMap>

    </v-content>


    <v-dialog v-model="dialog" persistent max-width="600px">
      <v-card >
        <v-card-title v-bind:color="dialog_color" >
          <span class="headline" >Station : {{station.name}} {{ getStatus(station.pm25,station.dtm)}}</span>


        </v-card-title>


        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>

              <v-flex xs6 sm6 md6>
                <v-text-field color="black"
                  :disabled="true"
                  v-model="station.pm25"
                  label="PM2.5 µg./m3"></v-text-field>
              </v-flex>
              <v-flex xs6 sm6 md6>
                <v-text-field v-bind:color="text_color"
                  :disabled="true"
                  v-model="station.pm10"
                  label="PM10 µg./m3"
                  required
                ></v-text-field>
              </v-flex>

              <v-flex xs6 sm6 md6>
                <v-text-field v-bind:color="text_color"
                  :disabled="true"
                  v-model="station.temp"
                  label="Temperature (°C)"
                  required
                ></v-text-field>
              </v-flex>
              <v-flex xs6 sm6 md6>
                <v-text-field v-bind:color="text_color"
                  :disabled="true"
                  v-model="station.humi"
                  label="Humidity (%)"
                  required
                ></v-text-field>
              </v-flex>
              <v-flex xs6 sm6 md6>
                <v-text-field v-bind:color="text_color"
                  :disabled="true"
                  v-model="station.dtm"
                  label="Last Update"
                  required
                ></v-text-field>
              </v-flex>




            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="black" flat @click="dialog = false">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    </v-app>
</template>


<script>
    import Vue from 'vue'
    import * as VueGoogleMaps from 'vue2-google-maps'
    import VueAnalytics from 'vue-analytics'

    Vue.use(VueAnalytics, {
    id: 'UA-136776555-1'
    })


 Vue.use(VueGoogleMaps, {
   load: {
     key: 'AIzaSyAP643J8qbUflgApZNfsbfUeA1r3pZ5AGs',
     libraries: 'places', // This is required if you use the Autocomplete plugin
     // OR: libraries: 'places,drawing'
     // OR: libraries: 'places,drawing,visualization'
     // (as you require)

     //// If you want to set the version, you can do so:
     // v: '3.26',
   },

   //// If you intend to programmatically custom event listener code
   //// (e.g. `this.$refs.gmap.$on('zoom_changed', someFunc)`)
   //// instead of going through Vue templates (e.g. `<GmapMap @zoom_changed="someFunc">`)
   //// you might need to turn this on.
   // autobindAllEvents: false,

   //// If you want to manually install components, e.g.
   //// import {GmapMarker} from 'vue2-google-maps/src/components/marker'
   //// Vue.component('GmapMarker', GmapMarker)
   //// then disable the following:
   // installComponents: true,
 })


import axios from 'axios'
import moment from 'moment'
 
export default {
  name: 'map',
    data() {
        return {
            //api_url :'http://192.168.10.203:8081/api/v1/station',
            api_url :'https://www.phitloktoday.com/api/v1/station',
            // api_url :'https://www.iotplus.cloud/api/v1/station',
            markers :[],
            //     {aqi:109, position: { lat:16.808225,lng:100.291653}},
            //     {aqi:110, position: { lat:16.818225,lng:100.371653}},
            //     {aqi:111, position: { lat:16.798225,lng:100.201653}},
            //     {aqi:112, position: { lat:16.758225,lng:100.271653}},
            //     {aqi:113, position: { lat:16.845225,lng:100.271653}}
            // ],

            srcicon:[],
            mapOptions:{
                disableDefaultUI : true
            },
            dialog:false ,
            dialog_color:"rgb(168,224,95)",
            text_color:"rgb(255,255,255)",
            station_index : 0,
            station :{
              name : "--",
              serial : "--",
              pm25 : "--",
              pm10 : "--",
              temp : "--",
              humi : "--",
              dtm : "--",
            },
            bg_colors : ["rgb(218,218,218)","rgb(47,155,0)", "rgb(168,224,95)", "rgb(255,215,0)", "rgb(255,169,104)", "rgb(254,106,105)"],
            txt_colors : ["rgb(140,140,140)","rgb(106,236,50)", "rgb(0,139,0)", "rgb(165,127,35)", "rgb(178,34,34)", "	rgb(175,44,59)"],
            description : ["--","ดีมาก", "ดี", "ปานกลาง", "เริ่มมีผลต่อสุขภาพ", "มีผลกระทบต่อสุขภาพ"],
            style_modal : '',
            sideNav: false,
            menuItems: [
                // {icon: 'map', title: 'Map', link: '/map'},
                // {icon: 'people', title: 'Sponser', link: '/sponser'},
                //{icon: 'person', title: 'Profile', link: '/profile'},
                // {icon: '', title: 'About Us',  link: '/aboutus'}
            ],
            // LPN
            // mapCenter: { lat: 13.7124112, lng: 100.6060072 },
            // mapZoom : 15

            // PHS
            mapCenter : {lat:16.818225, lng:100.271653},
            mapZoom : 11

        }
    },
    mounted () {

        this.loadStation()
        setInterval(
            function(){
                this.loadStation()
            }.bind(this)
            ,60000
        )
    },
    methods : {
        display(i){
          this.dialog = true
          this.station_index = i
          this.station = this.markers[i]

          this.dialog_color = this.bg_colors[this.getScorePM25(this.station.pm25,this.station.dtm)]
          this.text_color = this.txt_colors[this.getScorePM25(this.station.pm25,this.station.dtm)]
        },
        loadStation(){
            axios.post(this.api_url,).then((response)=>{
                this.markers = []
                
                response.data.forEach((item)=>{
                    this.markers.push({
                        aqi:item.pm25,
                        pm10:item.pm10,
                        pm25:item.pm25,
                        temp:item.temp,
                        humi:item.humi,
                        serial:item.serial,
                        name:item.name,
                        dtm:item.dtm,
                        position:{ lat:parseFloat(item.lat), lng:parseFloat(item.lng)}
                    })
                    this.station = this.markers[this.station_index]
                })
            })
        },
        getIcon(m){

            return 'data:image/svg+xml;utf-8, \
            <svg width="40px" height="40px" viewBox="0 0 54.4 54.4" xmlns="http://www.w3.org/2000/svg"> \
                <circle  fill="'+ this.bg_colors[this.getScorePM25(m.pm25,m.dtm)]+'" cx="27.2" cy="27.2" r="27.2"/> \
                <text dx="27" dy="35" text-anchor="middle" fill="'+ this.txt_colors[this.getScorePM25(m.pm25,m.dtm)]+'" style="font-size:20px;  font-family: Arial, Verdana; font-weight: bold">'+this.getText(m.pm25,m.dtm)+'</text>\
            </svg>'


        },
		getScorePM25(pm25,lu)
		{

            var mlu = moment(lu)
            var now = moment()

            var diffTime = now.diff(mlu)
            var diffMin = moment.duration(diffTime).as('minutes')



            if (diffMin > 5)
                return 0




            if (pm25 <= 25)
                return 1
            else if (pm25 <= 50)
                return 2
            else if (pm25 <= 75)
                return 3
            else if (pm25 <= 150)
                return 4
            else 
                return 5

        },
        getText(pm25,lu)
        {
            if (this.getScorePM25(pm25,lu) == 0) 
                return "--"
            else
                return pm25

        },
        getStatus(pm25,lu)
        {
            if (this.getScorePM25(pm25,lu) == 0) 
                return "(Offline)"
            else
                return " - คุณภาพอากาศ " +  this.description[this.getScorePM25(pm25,lu)]

        },
        track () {
        this.$ga.page('/map')
        }
    }
    
}
</script>
